import React, { Component } from "react";
import Axios from "axios";
import SelectTable from "./SelectTable";

class Post extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  componentDidMount() {
    let id = this.props.location.state.id;
    let token = this.props.location.state.access_token;
    // console.log(this.props.location.state);
    Axios.get(
      `https://graph.facebook.com/v6.0/${id}/comments?access_token=${token}`
    ).then(response => {
      this.setState({ data: response.data.data });
    });
  }

  handleChangeState = id => {
    let newState = this.state.data;
    let newData = newState.filter(item => {
      return item.id !== id;
    });
    this.setState({ data: newData });
  };

  showCommentList = () => {
    let id = this.props.location.state.id;
    let token = this.props.location.state.access_token;
    Axios.get(
      `https://graph.facebook.com/v6.0/${id}/comments?access_token=${token}`
    ).then(response => {
      this.setState({ data: response.data.data });
      // console.log(this.state.data);
    });
  };
  render() {
    return (
      <div>
        <SelectTable
          id={this.props.location.state.id}
          token={this.props.location.state.access_token}
        />
      </div>
    );
  }
}

export default Post;
