import React, { useState } from "react";
import "./App.css";
import FacebookLogin from "react-facebook-login";
import axios from "axios";
import moment from "moment";
import "antd/dist/antd.css";
import "moment/locale/vi";
import Tabular from "./components/Tabular";

import { Select } from "antd";
import { Link } from "react-router-dom";

moment.locale("vi");
function App() {
  const [facebookName, setFBName] = useState([]);
  const [pageToken, setToken] = useState(null);
  const [posts, setPosts] = useState([]);

  const responseFacebook = response => {
    fetchUserFeed(response.accessToken);
  };

  const fetchUserFeed = access => {
    axios
      .get(`https://graph.facebook.com/v6.0/me/accounts?access_token=${access}`)
      .then(response => {
        // setFBName(response.data.data[0].name);
        setFBName(response.data.data);
        // console.log("Good");
        return axios.get(
          `https://graph.facebook.com/v6.0/${response.data.data[0].id}/feed/?access_token=${response.data.data[0].access_token}`
        );
      })
      .then(res => {
        // setPosts(res.data.data);
      });
  };

  const handlePageChange = event => {
    let pageData = event.split(" ");
    setToken(pageData[1]);
    // console.log(pageToken);
    // console.log(pageData);

    axios
      .get(
        `https://graph.facebook.com/v6.0/${pageData[0]}/feed/?access_token=${pageData[1]}`
      )
      .then(res => {
        setPosts(res.data.data);
      })
      .catch(error => {
        console.log(error);
      });
  };

  return (
    <div className="App">
      <h1>
        <Link to="/">Comment Delete Tool</Link>
      </h1>
      <p>Powered by Maximusss - Leauge Of Legends Support Semi-pro gamer.</p>
      {facebookName.length > 0 ? (
        <div>
          <h3>
            Fanpage:{" "}
            <Select onChange={handlePageChange} style={{ width: 500 }}>
              {facebookName.map(item => {
                return (
                  <Select.Option
                    key={item.id}
                    value={item.id + " " + item.access_token}
                  >
                    {item.name}
                  </Select.Option>
                );
              })}
            </Select>
          </h3>
          <Tabular data={posts} token={pageToken} />
        </div>
      ) : (
        <FacebookLogin
          appId="2623573851104227"
          autoLoad={true}
          size="small"
          fields="email,picture,pages_show_list,manage_pages,publish_pages,ads_management,public_profile"
          callback={responseFacebook}
        />
      )}
    </div>
  );
}

export default App;
