import React from "react";
import moment from "moment";
import "antd/dist/antd.css";
import "moment/locale/vi";
import { Table } from "antd";
import { Link } from "react-router-dom";

const Tabular = ({ data, token }) => {
  const columns = [
    {
      title: "Mã bài viết",
      dataIndex: "id",
      key: "id"
    },
    {
      title: "Thời gian tạo",
      key: "created_time",
      render: (text, record) => (
        <span>{moment(record.created_time).format("LLLL")}</span>
      )
    },
    {
      title: "Thao tác",
      key: "action",
      render: (text, record) => (
        <Link
          // onClick={() => console.log("Token của tôi là " + token)}
          to={{
            pathname: `/post/${record.id}`,
            state: { id: record.id, access_token: token }
          }}
        >
          Chạy
        </Link>
      )
    }
  ];
  return (
    <div>
      <Table
        style={{ margin: 20 }}
        size="middle"
        footer={() => `Danh sách bao gồm ${data.length} bài viết`}
        dataSource={data}
        columns={columns}
        rowKey="id"
      />
    </div>
  );
};

export default Tabular;
